<template>
	<form @submit.prevent='updateData(item.id, elem)' class="renderInfo">
		<label :for="item.id">{{item.title | capitalize}}:</label>
		
		<vue-editor v-if='item.type === "textarea"' v-model="elem.value"></vue-editor>
		<preview-image v-else-if='item.type ==="image"' @getImg='getSrc' :currentImage='item.value' />
		<input v-else :id="item.id" class="form-control" v-model='elem.value' :type="elem.type">

		<button type="submit" class="admin-button">Оновити {{elem.title}}</button>
	</form>
	<!-- /.renderInfo -->
</template>
<script>
	import { VueEditor } from 'vue2-editor';
	const PreviewImage = () => import('@/components/PreviewImage');

	export default {
		name: 'renderInfo',
		props: ['item'],
		data() {
			return {
				elem: {
					title: this.item.title,
					type: this.item.type,
					value: this.item.value,
				},
			}
			
		},
		methods: {
			updateData(id, elem) {
				const createdAt = new Date();
				this.$_db.collection('personal').doc(id).update({createdAt, ...elem });
			},
			getSrc (data) {
		        this.elem.value = data;
		    },
		},
		filters: {
			capitalize(value) {
				return value[0].toUpperCase() + value.slice(1);
			}
		},
		components: {
			VueEditor,
			PreviewImage
		}

	}
</script>
<style lang='sass' scoped="true">
	textarea
		height: 200px
</style>